<template>
	<div class="orderAddress">
		<p class="module_title">收货人信息</p>
		<div class="address_item">
			<div class="inner">
				<div class="item_top">
					<span>{{ address.username }}</span>
					<span>{{ address.tel }}</span>
				</div>
				<div class="item_address">
					<p>{{ address.address }}</p>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	import {
		getAddressByUsername
	} from 'network/order.js'
	export default {
		data() {
			return {
				address: {}
			}
		},
		mounted() {
			this.getAddressByUsername()
		},
		methods: {
			getAddressByUsername() {
				getAddressByUsername(this.username).then(res => {
					this.address = res.data
				})
			}
		},
		computed: {
			username() {
				return this.$store.state.user ? this.$store.state.user.username : null
			}
		}
	}
</script>

<style lang="scss" scoped>
	.orderAddress {
		margin: 20px 0;
		box-sizing: border-box;
		padding: 19px;
		padding-bottom: 25px;
		background-color: #FFFFFF;

		.module_title {
			height: 30px;
			line-height: 30px;
			padding: 5px 0;
			font-size: 20px;
			color: #333333;
		}

		.address_item {
			height: 157px;
			width: 271px;
			cursor: pointer;
			background: url(data:image/png;base64,) no-repeat;

		}

		.inner {
			.item_top {
				display: flex;
				padding: 30px 22px 15px 22px;

				span {
					width: 100%;
					display: inline-block;
				}
			}

			.item_address {
				// w
				// padding-left: 22px;
				padding: 0 22px;
			}
		}
	}
</style>
